import React from "react";
import ContactFormComp from '../components/ContactFormComp';

import {SlideshowZoomIn, SlideshowZoomOut, SlideshowIndicator } from '../components/SlideshowComp';

import {Header, SettingDefaultValue } from '../components/PageComp';
