import React,{useState} from 'react'
import { FaStar } from "react-icons/fa"

export const SettingDefaultValue =({name = "Setting Parameter Default Value "}) =>(
   <>
   <Header title="Setting Parameter Default Value" refer="refer to components/PageCom"/>
   <h3>Value:  [ {name} ]</h3>
   </>
)

export const Header =(props) =>(
    <>
    <h2>{props.title} [calling Header]</h2>
    <h3>[{props.refer}]</h3>
    </>
)

export const Main = (props) => (
         
    <section>
    <h3> We serve the most {props.adjective} food around [Main SEction] </h3>
    <p>1: Import image file at the top and use it with React expression {}</p>
    <p>2: Place images inside public foler: syntac: process.env.PUBLIC_URL </p>
    {/* <img src={restaurant} height={400} alt="napkin and fork at table"/> */}


    <img src={process.env.PUBLIC_URL + '/images/restaurant.jpg'} height={400} alt="napkin and fork at table"/>
    {/* <img src="https://avatars.githubusercontent.com/u/66965736?v=4" height={400} alt="chef"/>  */}
   <h2>{props.serveTime} Menu</h2>
    <ul style={{textAlign:"left"}}>
   { props.dishes.map( (item) =>
     <li key={item.id}> {item.title} </li>
   )}

    </ul>
  </section>

)


export const Footer =() =>(
    <>
    <h2>[footer Section]</h2>
    <h3>[refert to components/footer]</h3>
    </>
)
//npm install react-icons
const createArray = (length) => [...Array(length)]

function Star({selected = false, onSelect}){
    return <FaStar color={selected?"red": "gray" } onClick={onSelect}/>    
}
export function StarRating({count = 5}){
   // console.log("Start: ",count)
   const [selected, setSelected ] = useState(0);

    return (
    <>
    {createArray(count).map((n,i) =>( 
     <Star  key ={i} 
      selected={selected > i} 
      onSelect ={()=> setSelected(i+1)}/> 
     ))}<br/>
     <p> { selected } of {count}</p>
     </>
    )
}


